@use "base/variables" as v;
@use "base/mixins" as m;

$width_babycloud_ip_1: 3vw;
$width_babycloud_ip_2: 14vw;
$width_babycloud_ip_3: 11vw;
$width_babycloud_ip_4: 5vw;
$width_babycloud_ip_5: 6.5vw;
$width_babycloud_ip_6: 8vw;
$width_babycloud_ip_7: 4.5vw;
$width_babycloud_ip_8: 3vw;
$width_babycloud_ip_9: 3vw;
$width_babycloud_ip_10: 3vw;
$width_babycloud_ip_11: 3vw;
$width_babycloud_ip_12: 3vw;
$width_babycloud_ip_13: 3vw;

.babycloud-ip-admin-body,
.babycloud-ip-body {

    .thead-babycloud_ip {
        background-color: v.$white;

        th {
            text-align: center;
        }
    }

    .thead {
        background-color: v.$blue;
        font-weight: normal;
        color: v.$white;
    }

    .thead-2 {
        font-size: calc(0.6rem + 0.25vw);
    }

    td {
        font-size: calc(0.6rem + 0.25vw);
    }

    .td-info-title {
        background-color: v.$white;
        color: v.$pink;
        border-radius: 50rem;
        text-align: center;
    }

    .td-select {
        width: 7vw;
    }

    .row-first {
        background-color: darken(v.$gray, 5%);
    }

    td {
        padding-top: 0 !important;
        padding-bottom: 0 !important;
    }

    .table-colgroup {
        col {
            &:nth-child(1) {
                width: $width_babycloud_ip_1;
            }

            &:nth-child(2) {
                width: $width_babycloud_ip_2;
            }

            &:nth-child(3) {
                width: $width_babycloud_ip_3;
            }

            &:nth-child(4) {
                width: $width_babycloud_ip_4;
            }

            &:nth-child(5) {
                width: $width_babycloud_ip_5;
            }

            &:nth-child(6) {
                width: $width_babycloud_ip_6;
            }

            &:nth-child(7) {
                width: $width_babycloud_ip_7;
            }

            &:nth-child(8) {
                width: $width_babycloud_ip_8;
            }

            &:nth-child(9) {
                width: $width_babycloud_ip_9;
            }

            &:nth-child(10) {
                width: $width_babycloud_ip_10;
            }

            &:nth-child(11) {
                width: $width_babycloud_ip_11;
            }

            &:nth-child(12) {
                width: $width_babycloud_ip_12;
            }

            &:nth-child(13) {
                width: $width_babycloud_ip_13;
            }
        }
    }

    .table-scroll {
        $width_babycloud_ip__extra: 0.75vw;
        max-width: $width_babycloud_ip_1 + $width_babycloud_ip_2 + $width_babycloud_ip_3 + $width_babycloud_ip_4 + $width_babycloud_ip_5 + $width_babycloud_ip_6 + $width_babycloud_ip_7 + $width_babycloud_ip_8 + $width_babycloud_ip_9 + $width_babycloud_ip_10 + $width_babycloud_ip_11 + $width_babycloud_ip_12 + $width_babycloud_ip_13 + $width_babycloud_ip__extra;
    }

}

